html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  // font-family: 'Mont-DEMOExtraLight', sans-serif;
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 400;
}

img {
  // max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}


h1,
h2,
h3,
h4,
p {
  margin: 0;
}

input,
textarea,
button {
  //font-family: inherit;
  outline: none;

}

textarea {
  resize: none;
  outline: none;
}


button {
  padding: 0;
}

svg,
svg path {
  transition: $transition;
}






.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none !important;

  @include mobile {
    display: block !important;
  }
}

.for-desktop {
  display: none !important; 

  @include desktop {
    display: block !important;
  }
}



.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.btn-secondary-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-end;

  &.__presentation-info {
    position: absolute;
    bottom: 293px;
    justify-content: flex-start;
    left: 26%;

    @media (max-width: 1680px) {
      bottom: 381px;
    left: 33%;
    }

    @media (max-width: 1280px) {
      bottom: 407px;
    left: 141px;
    }

    @media (max-width: 991px) {
      display: none;
  }

  }

  &.__routes-info {
    @media (max-width: 1199px) {
      margin-top: 4%;
    }
    @media (max-width: 767px) {
        display: none;
    }
  }


}



