.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}




.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);

}


// burger
.burger {

  position: relative;
  width: 50px;
  height: 37px;
  cursor: pointer;

  &.__header {
    margin-right: 1%;
  }

  @include desktop {
    display: none;
}
}


.burger span,
.burger span:before,
.burger span:after {
width: 50px;
position: absolute;
height: 2px;
background: white;
transition: .3s;
}


.burger span:before {
content: "";
top: 10px;
transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}


.burger span {
top: 20px;
}


.burger span:after {
content: "";
bottom: 10px;
transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}


.burger span.active:before {
content: "";
top: 0px;
transform: rotate(45deg);
transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}


.burger span.active:after {
content: "";
bottom: 0;
transform: rotate(-45deg);
transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}


.burger span.active {
background: rgba(0, 0, 0, 0);
//z-index: 110;
}



// close btn 

  
.close {

  @media (min-width: 992px) {
    display: none;
  }

  @media (max-width: 991px) {
    position: absolute;
    right: 20px;
    top: 20px;
  }
}

  // drop down menu
  .drop-down-menu {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 82px 65px;
    transition: $transition;
  
    &.__header--mobile {
      overflow: scroll;


  
      @include mobile {
        //display: none;
        // background-color: $color-drop-down-list;
        width: 100%;
        max-width:300px;
        position: fixed;
        visibility: hidden;
        top: -100%;
        right: 0;
        padding: 65px 33px 45px 33px;
      }
  
      &.active {
  
        @media (min-width: 992px) {
          z-index: 2;
          visibility: visible;
          opacity: 1;
        }
  
        @media (max-width: 991px) {
          //display: none;
          visibility: visible;
          opacity: 1;
          top: 0;
          z-index: 100;
        }
      }
  
    }

    &__inner {

        &.__header--mobile {
            @include mobile {
                margin-bottom: 20px;
            }
        }

    }
  }





