  .slider-gallery {
    padding-bottom: 55px;

    .slick-list {
 @media (max-width: 767px) {
      // padding-left: 30%;
      // margin-left: -15%;
    }
    }

   
  }


  .slider-reviews {
   

      @media (min-width: 992px) {
        height: 225px;

  
        }

        
      @media (max-width: 991px) {
        height: 200px;
  
          margin-bottom: 15px;
    
          }

      @media (max-width: 767px) {
      height: 180px;

  
        }

      .slider-item {
        box-shadow: 0px 4px 30px rgba(51, 51, 51, 0.1);
        margin-left: 20px;
        padding: 20px;
        height: 87%;
        margin-right: 20px;

      }

      .slick-list {
        height:100%;

        .slick-track {
          height: 100%;
          align-items: center;

          img {
            height: 100%;
            width: 100%;

            @media (max-width: 991px) {
              object-fit: contain;
            }
          }
        }
      }

      .slick-arrow {
        position: absolute;
        
        // left: 50%;
  
        // top: 50%;
        // transform: translateX(-50%);
        z-index: 100;
        font-size: 0;
        // // margin-top: -45px;
       
        border: none;

        @media (min-width: 991px) {
          bottom: -45px;
          width: 40px;
          height: 40px;
        }
        @media (max-width: 991px) {
          top: -48px;
          width: 25px;
          height: 25px;
        }
    
        
      }

      .slick-arrow.slick-next{
        
        background: url(../img/arrow-right.svg) 0 0 no-repeat;
        background-size: cover;
         @media (min-width: 991px) {
          right: -20px;
        }
         @media (max-width: 991px) {
          right: 40px;
        }
    
      }

      .slick-arrow.slick-prev{
      
        background: url(../img/arrow-left.svg) 0 0 no-repeat;
        background-size: cover;
         @media (min-width: 991px) {
          left: 89%;
          right: 20px;
        }
         @media (max-width: 991px) {
          right: 72px;
        }
    
       
    
      }
  }

  .slider-gallery {
    @media (max-width: 475px) {
      padding: 0 10px 55px 10px;
    }
    .slick-arrow.slick-prev {
   

      @media (min-width: 992px) {
        bottom: -37px;
      }
      @media (max-width: 991px) {
        left: 33%;
        // bottom: 23px;
        bottom: 0;
      }
     
    }

    .slick-arrow.slick-next {
      @media (min-width: 992px) {
        bottom: -37px;
      }
      @media (max-width: 991px) {
        right: 33%;
        // bottom: 23px;
        bottom: 0;

      }
 

    }
    .slick-track {
      .slider-item {
        &.__gallery {

          // &:nth-child(2n) {
            img {

              @media (max-width: 991px) {
                height: 200px;
              }
              
            }
          // }
          
        }
      }

      .slick-slide {
        // &:nth-child(2n) {
         

          @media (min-width: 768px) {
            margin-left: 25px;
          }
        // }

        div {
          margin-bottom: 30px;
          height: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: $border-radius;

          @media (max-width: 767px) {
            margin-bottom: 15px;
          }

          picture,
          video,
          iframe {
            width: 100%;
            height: 85%;
            display: flex;
            align-items: center;
            justify-content: center;
          border-radius: $border-radius;


            img {
              height: auto;
              width: 100%;
          border-radius: $border-radius;

              // max-height: 500px;
            }
          }


          // &:nth-child(1) {
            // margin-left: 13px;
          // }

          // &:nth-child(2) {
          //   margin-left: 25px;
          // }
        }
      }
    
    }
  }